.signin {
    width: 100%; 
    height: 100%;
    padding-top: 0vh;

    .box-signin {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        // gap: 30px;

        img {
            width: 156px;
            height: 156px;
        }

        form {
            display: flex;
            flex-direction: column;
            gap: 15px;
            justify-content: center;
            align-items: center;
            background: #04724D;
            border-radius: 30px;
            width: 35vw;
            padding: 20px 2px 0 2px;
            box-shadow: rgba(126, 125, 125, 1) 0px 4px 75px,
            rgba(255, 255, 255, 0.12) 0px -12px 30px, rgba(255, 255, 255, 0.12) 0px 4px 6px,
            #04724d52 0px 12px 53px, #04724d5e 0px -3px 55px;

            h3 {
                font-family: 'Montserrat';
                font-style: normal;
                font-weight: 900;
                font-size: 30px;
                line-height: 40px;
                text-align: center;
                color: #FFFFFF;
            }

            label {
                font-family: 'Montserrat';
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 20px;
                color: #FFFFFF;
            }

            input::placeholder {
                color: black;
                height: 100%;
                padding-left: 10px;
                // height: 40px;
            }
            input {
                background: #DFFFE8;
                border-radius: 10px;
                border: 0;
                height: 40px;
                padding: 0;
            }

            .form-sign {
                display: flex;
                flex-direction: column;
                gap: 40px;
                width: 100%;
                padding: 0px 80px;

                .inputBox {
                    display: flex;
                    flex-direction: column;
                    gap: 7px;
                }

                .box-b {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    // gap: 30px;
                }

                a {
                    font-family: 'Montserrat';
                    font-style: normal;
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 40px;
                    text-align: center;
                    text-decoration-line: underline;
                    color: #FFFFFF;
                    background: transparent;
                    margin: 0;
                    text-transform: initial;
                }

                button {
                    font-family: 'Montserrat';
                    font-style: normal;
                    font-weight: 500;
                    font-size: 16px;
                    text-align: center;
                    color: #000000;
                    background: #FFFFFF;
                    border-radius: 10px;
                    width: 70%;
                    text-transform: initial;
                    // padding: 10px 10px;
                }

                .ggl-b {
                    display: flex !important;
                    justify-content: center;
                    border: 0px !important;
                    border-radius: 3px !important;
                    color: white !important;
                    cursor: pointer;
                    font-size: 19px !important;
                    width: calc(100% - 10px) !important;
                    user-select: none;
                    height: 50px !important;
                    background: transparent !important;
                    box-shadow: rgb(0 0 0 / 50%) 0px 0px 0px !important;
                    border: 0;
                    text-align: center !important;
                    align-items: center !important;
                    margin: 0 !important;
                    padding: 0  !important;
                }

                .ggl-b:hover {
                    text-decoration: underline;
                }
            }

        }
    }
}